import React,{useState} from 'react';
import Nav from "./nav"
import axios from "axios";

function Form() {
    const [input,setInput] = useState({
        firstname:'',
        lastname:'',
        email:'',
        college:'',
        domain:''
    })
    function handleChange(event){
       const {name,value} = event.target;
       setInput(prevInput =>{
           return{
               ...prevInput,
               [name]:value
           }
       })
    }
    function handleClick(event){
        event.preventDefault();
        const newApplication ={
            firstname:input.firstname,
            lastname:input.lastname,
            email:input.email,
            college:input.college,
            domain:input.domain
        };
       axios.post('https://softrate-intern-website.herokuapp.com/save',newApplication)
       .then(() =>{
           console.log("data sent to server");
       })
       .catch(() =>{
           console.log("data not sent");
       })
       alert("Application Form Submitted");
    }    
        
    
    return (
        <div>
            <Nav/>
        <div className="form-page">

            <h2 className="form-head">Please Fill The Form</h2>
            
        <div className="container p-3 my-3 border ">
        <div className="form-row"> 
        <form method="POST" action="/save">
                
        <div className="form-group col-md-6" >
          <label>FirstName:</label>
          <input className="form-control" type="text" autoComplete="off" onChange={handleChange} value={input.firstname} placeholder="Enter firstName" name="firstname" required/>
       </div>
      
       <div className="form-group col-md-6">
            <label >LastName:</label>
            <input className="form-control" type="text" autoComplete="off" onChange={handleChange} value={input.lastname} placeholder="Enter LastName" name="lastname" required/>   
       </div>  
           
      <div className="form-group col-md-6">
         <label>Email-id:</label>
          <input className="form-control" type="text" autoComplete="off" onChange={handleChange} value={input.email} placeholder="Enter email-id" name="email" required/>
       </div>
          
          <div className="form-group col-md-6" >
          <label>College-Name</label>
          <input className="form-control" type="text" autoComplete="off" onChange={handleChange} value={input.college} placeholder="Enter college name" name="college" required/>
          </div>

           <div className="form-group col-md-6">
          <label>Domain</label>
          <input className="form-control" type="text" autoComplete="off" onChange={handleChange} value={input.domain} placeholder="Enter Domain" name="domain" required />
          </div><br/> 
        
    
    
   
          <div><button type="submit" className="btn btn-primary mb-2"onClick={handleClick}>SUBMIT</button></div>
       </form>
                  
        </div>
       </div>
        </div>
        </div>
    )
}

export default Form
