import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import img from "C:/Users/LENOVO/OneDrive/Desktop/softrate/app/src/components/photo.jpg";
function nav() {
    return (
        <div>
           <nav className="navbar sticky-top navbar-expand-lg bg-dark">
             <div className="container">
             <a href="https://www.softrateindia.com/" class="logo">
              <img src={img} alt="logo of Softrate"/>
          
            </a>
               <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <i class="fas fa-bars"></i>
                </button>
                <div  className="collapse navbar-collapse" id="navbarSupportedContent">
                  <ul className="navbar-nav mr-auto w-100 justify-content-end">
                    <li className="nav-item active">
                    <a class="nav-link" href="#">Home <span class="sr-only">(current)</span></a>
                    </li>
                    <li class="nav-item">
                    <a class="nav-link" href="#">About</a>
                    </li>
                    <li class="nav-item">
                      <a class="nav-link" href="#">Contact</a>
                      </li>
                      <li class="nav-item">
                        <a class="nav-link" href="#">Services</a>
                        </li>
                        <li class="nav-item">
                          <a class="nav-link" href="#">Other</a>
                        </li>
                  </ul>
                </div>
             </div>
             </nav> 
        </div>
    )
}

export default nav

