import React from 'react'
import img from "C:/Users/LENOVO/OneDrive/Desktop/softrate/app/src/components/image.png";

import {Link} from "react-router-dom";
function intern() {
  
    return (
        <div>
        <div class="content">
        <div class="image">
        <img src={img} alt="image"/>
       </div>
        <div class="main-text">
       <h1>WE ARE HIRING!!</h1>
       <p><b> Softrate is now hiring interns from various domains</b></p>
       <h3>Available Domains</h3>
       <ul class="domain">
        <li>Web-Development</li>
        <li>Content-Writing</li>
        <li>App-Development</li>
        <li>Digital-Marketing</li>
       </ul>
         <Link to="/form" className="apply-btn">APPLY NOW</Link>
       </div>
       </div>
       </div>
        
    )
}

export default intern
